import { BrowserRouter, Navigate, Route, Routes } from 'react-router-dom';
import './App.css';
import DeviceList from './components/pages/DeviceList/DeviceList';
import AppBar from './components/shared/appbar/Appbar';
import DeviceCount from './components/pages/DeviceCount/DeviceCount';
import ClickPhoto from './components/pages/clickPhoto/ClickPhoto';

const App = () => {
  return (
    <div className="App min-h-screen">
      <BrowserRouter>
        <AppBar />
        <Routes>
          <Route path='/' element={<Navigate to="/click"/>}/>
          <Route path='/home' element={<DeviceList />}/>
          <Route path='/click' element={<ClickPhoto />}/>
          <Route path='/device/:id' element={<DeviceCount />}/>
      </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;
